@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
 }
 
 
 body{
   font-family: "montserrat", sans-serif;
 }
 
 .app{
   background-image: url(/weatherappcreate/static/media/cold-bg.da61c615.jpg);
   background-size: cover;
   background-position: bottom;
   transition: 0.4 ease-out;
 
 }
 .app.warm{
   background-image: url(/weatherappcreate/static/media/warm-bg.712152f6.jpg);
 
 }
 
 main{
 
 min-height: 100vh;
 background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba (0, 0, 0, 0.75));
 padding: 25px;
 
 }
 .search-box{
   width: 100%;
   margin: 0 0 75px;
   
  
 }
 
 .search-bar {
   display: block;
   width: 100%;
   padding:15px;
 
   -webkit-appearance: none;
 
      -moz-appearance: none;
 
           appearance: none;
   background: none;
   border: none;
   outline: none;
 
   background-color: rgba(255, 255, 255, 0.5);
   border-radius: 0px 0px 16px 16px ;
   margin-top: -25px;
   box-shadow:  0px 5px rgba(0, 0, 0, 0.2);
 
   color: #313131;
   font-size: 20px;
 
   transition: 0.4s ease;
 }
 .search-bar:focus{
   background-color: rgba(255, 255, 255, 0.75);
 
 }
 .location{
   color: #fff;
   font-size: 32px;
   font-weight: 500;
   text-align: center;
   text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
 }
 .date{
   color: #fff;
   font-size: 20px;
   font-weight: 300;
   font-style: italic;
   text-align: center;
   text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
 }
 .weather-box{
   text-align: center;
 }
 .temp{
   position: relative;
   display: inline-block;
   margin: 30px auto;
   background-color: rgba(255, 255, 255, 0.2);
   border-radius: 16px;
 
   padding: 15px 25px;
   color: #fff;
   font-size: 102px;
   font-weight: 900;
   text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
   text-align: center;
   box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
 
 }
  .weather{
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    text-shadow:3px 3px rgba(50, 50, 70, 0.5);
  }
 
 


  
